                      v-model="pax"
                    />
                    <div class="invalid-feedback">
                      <span id="pax-msg"></span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="col-sm-12">
              <div class="form-group form-check">
                <input
                  type="checkbox"
                  class="form-check-input"
                  id="specialtransport"
                  v-model="specialtransportactive"
                />
                <label
                  class="form-check-label"
                  for="specialtransport"
                >{{multilanguage.specialtransport[language]}}</label>
              </div>
            </div>
            <div v-show="specialtransportactive" class="col-sm-6 col-md-4 col-lg-2">
              <label
                for="bike"
                data-error="wrong"
                data-success="right"
                class="active"
              >{{multilanguage.bike[language]}}</label>
              <input
                id="bike"
                size="2"
