.occupation-container {
  padding-top: 1em;
}
/*Sets the maximum height of the modal body to 90% of the screen height*/
#tabsdestinoContent {
  max-height: 50vh;
  overflow-y: auto;
}

.destinocontent {
  max-height: 50vh;
  overflow-y: auto;
}

.modal-dialog {
  margin-top: 15%;
}
.search-text {
  font-size: 1.5rem;
  color: grey;
  text-align: center;
}
</style>
