<script>
import axios from "axios";
import myCaptcha from "vue-captcha";
import VueRecaptcha from "vue-recaptcha";
import modalAdded from '@/components/commons/CartAdded';
import helper from '@/script/helper';

const langfile = require("../../assets/lang/content.json");

export default {
  name: "Detalle",
  data: function() {
    return {
      helper:helper,
      status: null,
      detalles: null,
      titular: null,
      comentarios: null,
      telefono: null,
      email: null,
      vuelollegada: null,
      horallegada: null,
      incomingFlightOrigin: null,
      outgoingFlightDestination: null,
      vuelosalida: null,
      horasalida: null,
      coupon: null,
      loader: null,
      unmailing: false,
      language: globaldata.language?globaldata.language:"es",
      multilanguage: langfile.multilanguage,
      formattedhorasalida: null,
      formattedhoraentrada: null,
      reseolvedCaptcha: false,
      recaptchatoken: globaldata.recaptchatoken,
      captchaValidation: globaldata.captchaValidation,
      nocart: globaldata.nocart,
      termsaccepted: false,
      fechasalidaformat: null,
      fechaentradaformat: null,
      nobreakdown: globaldata.nobreakdown
    };
  },
  components: {
    "my-captcha": myCaptcha,
    VueRecaptcha,
    'modalAdded': modalAdded
  },
  methods: {
    verify: function() {
      // this.status = "submitting";
      this.$refs.recaptcha.execute();
    },
    onCaptchaVerified: function(recaptchaToken) {
      const _this = this;
      _this.status = "submitting";
      _this.$refs.recaptcha.reset();
      _this.book(recaptchaToken);
    },
    onCaptchaExpired: function() {
      this.$refs.recaptcha.reset();
    },
    captchaOk() {
      this.$data.reseolvedCaptcha = true;
    },
    goback(page) {
      switch (page) {
        case "formulario":
          this.$router.push({
            name: "main",
            query: {
              destino: this.$route.query.destino,
              destinoname: this.$route.query.destinoname,
              origen: this.$route.query.origen,
              origenname: this.$route.query.origenname,
              entrada: this.$route.query.entrada,
              salida: this.$route.query.salida,
              idioma: this.$route.query.idioma,
              bike: this.$route.query.bike,
              golf: this.$route.query.golf,
              ski: this.$route.query.ski,
              wheelchair: this.$route.query.wheelchair,
              bigluggage: this.$route.query.bigluggage,
              pax: this.$route.query.pax
            }
          });
          break;
        case "dispo":
          this.$router.push({
            name: "traslado_dispo",
            query: {
              destino: this.$route.query.destino,
              destinoname: this.$route.query.destinoname,
              origen: this.$route.query.origen,
              origenname: this.$route.query.origenname,
              entrada: this.$route.query.entrada,
              salida: this.$route.query.salida,
              idioma: this.$data.language,
              bike: this.$route.query.bike,
              golf: this.$route.query.golf,
              ski: this.$route.query.ski,
              wheelchair: this.$route.query.wheelchair,
              bigluggage: this.$route.query.bigluggage,
              pax: this.$route.query.pax
            }
          });
          break;

        default:
      }
    },
    procesar: function(response) {
      if (response.data.statusCode === 200) {
        this.$router.push({
          name: "ok",
          query: {
            bookingId: response.data.bookingId,
            availableservices: response.data.availableServices,
            paymenturl: response.data.paymentUrl
          }
        });
      } else {
        this.$router.push({ name: "ko", query: { msg: response.data.msg } });
      }
    },
    addtocart: function() {
      var d = this.$data;
      var reference = helper.gettimereference();
      if (this.checkForm()) {
        var cartline = {
          bookingtype: "traslado",

          leadName: this.$data.titular,
          commentsToProvider: this.$data.comentarios,
          contactPhone: this.$data.phone,
          email: this.$data.email,
          incomingFlightNumber: this.$data.vuelollegada,
          incomingFlightTime: this.formattime(
            this.$route.query.entrada,
            this.$data.horallegada
          ),
          incomingFlightOrigin: this.$data.incomingFlightOrigin,
          outgoingFlightNumber: this.$data.vuelosalida,
          outgoingFlightTime: this.formattime(
            this.$route.query.salida,
            this.$data.horasalida
          ),
          outgoingFlightDestination: this.$data.outgoingFlightDestination,
          bike: this.$route.query.bike,
          golf: this.$route.query.golf,
          ski: this.$route.query.ski,
          wheelchair: this.$route.query.wheelchair,
          bigLuggages: this.$route.query.bigluggage,

          rateKeys: d.key,
          itemDescription: this.mountdescription(),

          total: d.detalles.total,
          paymentLines: d.detalles.paymentLines,
          cancellationCosts: d.detalles.cancellationCosts,
          prices: d.detalles.priceLines,
          remarks: d.detalles.remarks,
          arrivalInstructions :d.detalles.arrivalInstructions,
          departureInstructions: d.detalles.departureInstructions,
          cancellationFreeDate: d.detalles.cancellationFreeDate,
          services: [],
          bookingReference: reference
        };

        var currentCart = localStorage.qtse_cart
          ? JSON.parse(localStorage.getItem("qtse_cart"))
          : [];
        currentCart.push(cartline);
        localStorage.setItem("qtse_cart", JSON.stringify(currentCart));

        this.storageData();

        this.$refs.modalAdded.showModal()
      }
    },
    mountdescription: function() {
      var _this = this;
      var desc =
        '<div class="title">' +
        this.$route.query.transfername +
        '</div>' +
        '<div class="line1"> <span><b style="margin-right:0.5em;">' +
        _this.multilanguage.incoming[this.$data.language] +
        ': </b>'  +
        helper.formatDatetoview(this.$route.query.entrada) +
        ' </span></div>' +
        '<div class="line1"> <span><b style="margin-right:0.5em;">' +
        _this.multilanguage.outgoing[this.$data.language] +
        ': </b> ' +
        helper.formatDatetoview(this.$route.query.salida) +
        '</span></div>' +
        '<div class="line2"> <b> ' +
        _this.multilanguage.origin[this.$data.language] +
        ': </b> ' +
        this.$route.query.origenname +
        '</div>' +
        '<div class="line2"> <b> ' +
        this.multilanguage.destination[this.$data.language] +
        ': </b> ' +
        this.$route.query.destinoname +
        '</div>' +
        '<div class="line3"><b>' +
        _this.multilanguage.pax[this.$data.language] +
        ':</b>' +
        this.$route.query.pax +
        ' </div>';
      return desc;
      /*<div v-if="this.$route.query.bike >0"       style="margin-bottom:0.5em; display:inline-block" class="valign-wrapper"><span><b style="margin-right:0.5em;"> {{multilanguage.bike[language]}}:</b> {{this.$route.query.bike}} | </span></div>
          <div v-if="this.$route.query.golf >0"       style="margin-bottom:0.5em; display:inline-block" class="valign-wrapper"><span><b style="margin-right:0.5em;"> {{multilanguage.golf[language]}}:</b> {{this.$route.query.golf}} | </span></div>
          <div v-if="this.$route.query.ski >0"        style="margin-bottom:0.5em; display:inline-block" class="valign-wrapper"><span><b style="margin-right:0.5em;"> {{multilanguage.ski[language]}}:</b> {{this.$route.query.ski}} | </span></div>
          <div v-if="this.$route.query.wheelchair >0" style="margin-bottom:0.5em; display:inline-block" class="valign-wrapper"><span><b style="margin-right:0.5em;"> {{multilanguage.wheelchair[language]}}:</b> {{this.$route.query.wheelchair}} | </span></div>
          <div v-if="this.$route.query.bigluggage >0" style="margin-bottom:0.5em; display:inline-block" class="valign-wrapper"><span><b style="margin-right:0.5em;"> {{multilanguage.bigluggage[language]}}:</b> {{this.$route.query.bigluggage}} | </span></div>
          </div>
          </div>*/
    },
    storageData: function() {
      let maindata = {
        leadName: this.$data.titular,
        contactPhone: this.$data.telefono,
        email: this.$data.email,
        startdate: this.$route.query.entrada,
        enddate: this.$route.query.salida
      };
      localStorage.setItem("qtse_maindatastorage", JSON.stringify(maindata));
    },


    book: function(recaptchaToken) {
      //e.preventDefault()
      var _this = this;
      var d = this.$data;

      var reference = helper.gettimereference();

      if (this.checkForm()) {
        d.loader = true;
        helper.scrollToId("main-app");
        axios({
          method: "put",
          url: globaldata.baseurl + "/transfer/booking",
          timeout: 1000 * 30,
          data: {
            language: _this.$data.language,
            key: _this.$route.query.key,
            captchaToken: recaptchaToken,
            bookingReference: reference,
            leadName: _this.$data.titular,
            commentsToProvider: _this.$data.comentarios,
            contactPhone: _this.$data.telefono,
            email: _this.$data.email,
            incomingFlightNumber: _this.$data.vuelollegada,
            incomingFlightTime: _this.formattime(
              _this.$route.query.entrada,
              _this.$data.horallegada
            ),
            incomingFlightOrigin: _this.$data.incomingFlightOrigin,
            outgoingFlightNumber: _this.$data.vuelosalida,
            outgoingFlightTime: _this.formattime(
              _this.$route.query.salida,
              _this.$data.horasalida
            ),
            outgoingFlightDestination: _this.$data.outgoingFlightDestination,
            bike: _this.$route.query.bike,
            golf: _this.$route.query.golf,
            ski: _this.$route.query.ski,
            wheelchair: _this.$route.query.wheelchair,
            bigLuggages: _this.$route.query.bigluggage,
            mailingUnwanted: _this.$data.unmailing
          }
        })
          .then(function(response) {
            d.loader = false;
            _this.procesar(response);
          })
          .catch(function(error) {
            d.loader = false;
            _this.$router.push({ name: "ko", query: { msg: error } });
            console.log(error);
          });
      }
    },
    checkForm: function(e) {
      this.cleanErrors();
      var existsError = false;
      if (this.$data.detalles.terms && this.$data.nocart) {
        if (this.$data.termsaccepted !== true) {
          this.setValidationError(
            "termandcondcheck",
            "termandcondcheck-msg",
            this.multilanguage.pleaseacceptterms[this.language]
          );
          this.setValidationError(
            "termandcondcheck2",
            "termandcondcheck2-msg",
            this.multilanguage.pleaseacceptterms[this.language]
          );
          existsError = true;
        }
      }

      if (this.$route.query.entrada) {
        if (this.$data.horallegada === null || this.$data.horallegada === "") {
          this.setValidationError(
            "horallegada",
            "horallegada-msg",
            this.multilanguage.incomingdateempty[this.language]
          );
          existsError = true;
        } else {
          if (!helper.validateTime(this.$data.horallegada)) {
            this.setValidationError(
              "horallegada",
              "horallegada-msg",
              this.multilanguage.invalidTimeFormat[this.language]
            );
            existsError = true;
          }
        }

        if (
          this.$data.vuelollegada === null ||
          this.$data.vuelollegada === ""
        ) {
          this.setValidationError(
            "vuelollegada",
            "vuelollegada-msg",
            this.multilanguage.incomingflightempty[this.language]
          );
          existsError = true;
        }

        if (
          this.$data.incomingFlightOrigin === null ||
          this.$data.incomingFlightOrigin === ""
        ) {
          this.setValidationError(
            "origenllegada",
            "origenllegada-msg",
            this.multilanguage.incommingflightoriginempty[this.language]
          );
          existsError = true;
        }
      }

      if (this.$route.query.salida) {
        if (this.$data.horasalida === null || this.$data.horasalida === "") {
          this.setValidationError(
            "horasalida",
            "horasalida-msg",
            this.multilanguage.outgoingdateempty[this.language]
          );
          existsError = true;
        } else {
          if (!helper.validateTime(this.$data.horasalida)) {
            this.setValidationError(
              "horasalida",
              "horasalida-msg",
              this.multilanguage.invalidTimeFormat[this.language]
            );
            existsError = true;
          }
        }

        if (this.$data.vuelosalida === null || this.$data.vuelosalida === "") {
          this.setValidationError(
            "vuelosalida",
            "vuelosalida-msg",
            this.multilanguage.outgoingflightempty[this.language]
          );
          existsError = true;
        }
        if (
          this.$data.outgoingFlightDestination === null ||
          this.$data.outgoingFlightDestination === ""
        ) {
          this.setValidationError(
            "destinosalida",
            "destinosalida-msg",
            this.multilanguage.outgoingflightdestempty[this.language]
          );
          existsError = true;
        }
      }

      if (this.$data.telefono === null || this.$data.telefono === "") {
        this.setValidationError(
          "telefono",
          "telefono-msg",
          this.multilanguage.phoneempty[this.language]
        );
        existsError = true;
      }

      if (this.$data.email === null || this.$data.email === "") {
        this.setValidationError(
          "email",
          "email-msg",
          this.multilanguage.erroremailempty[this.language]
        );
        existsError = true;
      } else {
        if (!helper.validateEmail(this.$data.email)) {
          this.setValidationError(
            "email",
            "email-msg",
            this.multilanguage.erroremailnotvalid[this.language]
          );
          existsError = true;
        }
      }

      if (this.$data.titular === null || this.$data.titular === "") {
        this.setValidationError(
          "name",
          "name-msg",
          this.multilanguage.errorleadnameempty[this.language]
        );
        existsError = true;
      }
      if (existsError) {
        return false;
      }
      return true;
    },
    setValidationError(field, msgField, msg) {
      if (field !== null && field !== " ") {
        var element = document.getElementById(field);
        element.classList.add("is-invalid");
        //element.classList.add('form-control')
        var att = document.createAttribute("aria-invalid");
        att.value = "true";
        element.setAttributeNode(att);
        element.focus();
      }
      if (msgField !== null && msgField !== " ") {
        var elementmsg = document.getElementById(msgField);

        elementmsg.innerHTML = msg;
      }
    },
    cleanErrors: function() {
      var element = document.getElementById("name");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }

      element = document.getElementById("email");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("horasalida");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("horallegada");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("telefono");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("horallegada");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("origenllegada");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("horasalida");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("vuelollegada");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("vuelosalida");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("destinosalida");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-control");
      }
      element = document.getElementById("termandcondcheck");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-check-input");
      }
      element = document.getElementById("termandcondcheck2");
      if (element) {
        element.setAttribute("aria-invalid", "false");
        element.setAttribute("class", "form-check-input");
      }
    },
    formattime: function(date, time) {
      if (time) {
        let timeArray = time.split(":");
        return timeArray[0] + timeArray[1];
      } else {
        return null;
      }
    },
    applyCoupon: function() {
      var d = this.$data;
      if (d.coupon && d.coupon !== null && d.coupon !== "") {
        this.getInfo();
      }
    },
    getStorageData: function() {
      let maindata = JSON.parse(localStorage.getItem("qtse_maindatastorage"));
      if(maindata !== null){
        if (maindata.leadName) {
          this.$data.titular = maindata.leadName;
        }
        if (maindata.contactPhone) {
          this.$data.telefono = maindata.contactPhone;
        }
        if (maindata.email) {
          this.$data.email = maindata.email;
        }
      }
    },

    getInfo: function() {
      var d = this.$data;
      var _this = this;
      d.loader = true;

      axios({
        method: "get",
        url:
          globaldata.baseurl +
          "/transfer/pricedetails/" +
          this.$route.query.key,
        params: {
          coupon: d.coupon,
          language: this.$data.language
        }
      })
        .then(function(response) {
          d.loader = null;
          d.detalles = response.data;

        })
        .catch(function(error) {
          console.log(error);
          d.loader = null;
        });
    }
  },
  mounted() {
    
    this.getInfo();
    this.$data.fechasalidaformat = helper.formatDatetoview(
      this.$route.query.salida
    );
    this.$data.fechaentradaformat = helper.formatDatetoview(
      this.$route.query.entrada
    );
    this.getStorageData();
    helper.scrollToId("main-app");
  }
};
